import { useNavigate, useParams,Link } from "react-router-dom";
import { useEffect, useState } from "react";
import axios from "axios";

export default function BoardInfo() {
  let Params = useParams();
  let [Lists, setList] = useState({});
  const navigator = useNavigate();

  const handlerDelete = (event)=>{
    if(window.confirm("?")){
        axios.delete("http://localhost/board/" + Params.BoardNo);
        navigator("/BoardComponent/BoardList");
    }
  }

  function CallAPI() {
    fetch("http://localhost/board/" + Params.BoardNo)
      .then((Result) => Result.json())
      .then((Result) => {
        setList(Result[0]);
      });
  }

  useEffect(() => {
    CallAPI();
  }, []);
  return (
    <>
      <form>
        <div>
          <span>
              : {Lists.boardNo}
          </span>
        </div>
        <div>
          <span>
              : {Lists.title}
          </span>
        </div>
        <div>
          <span>
              : {Lists.content} 
          </span>
        </div>
        <div>
          <span>
              : {Lists.wdt}
          </span>
        </div>
        <div>
          <span>
              : {Lists.writer}
          </span>
        </div>
        <Link to={"/BoardComponent/BoardUpdate/"+Params.BoardNo}>
        <button type="button"></button></Link>
        <button type="button" onClick={handlerDelete}></button>
      </form>
    </>
  );
}
