import { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button} from "react-bootstrap";

export default function ComponentUpdate(props) {
  let [List, setList] = useState([]);

  function callApi() {
    if(!props.id){
        return;
    }
    fetch("http://localhost/customer/" + props.id)
      .then((Result) => Result.json())
      .then((Result) => setList(Result[0]));
  }

  useEffect(() => {
    callApi();
  }, [props]);

  return (
    <>
      <form>
        <Button type="button" className="btn-success"></Button>
        <Button type="reset"></Button>
        <Button type="reset"></Button>
        <div>
          <input name="id" type="text" value={List.id}></input>
        </div>
        <div>
          <input name="name" type="text" value={List.name}></input>
        </div>
        <div>
          <input name="email" type="text" value={List.email}></input>
        </div>
        <div>
           <input name="phone" type="text" value={List.phone}></input>
        </div>
      </form>
    </>
  );
}
